.gallery {
  margin-bottom: 1.5em; }

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%; }
  .gallery-columns-2 .gallery-item {
    max-width: 50%; }
  .gallery-columns-3 .gallery-item {
    max-width: 33.33%; }
  .gallery-columns-4 .gallery-item {
    max-width: 25%; }
  .gallery-columns-5 .gallery-item {
    max-width: 20%; }
  .gallery-columns-6 .gallery-item {
    max-width: 16.66%; }
  .gallery-columns-7 .gallery-item {
    max-width: 14.28%; }
  .gallery-columns-8 .gallery-item {
    max-width: 12.5%; }
  .gallery-columns-9 .gallery-item {
    max-width: 11.11%; }

.gallery-caption {
  display: block; }

.card-pricing .btn,
.card-pricing .wpcf7 input[type=submit], .wpcf7 .card-pricing input[type=submit] {
  padding: 1rem 2rem;
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase; }

.btn.wpcf7-form-control, .wpcf7 input.wpcf7-form-control[type=submit] {
  color: #fff;
  background-color: #fe8b0c; }
  .btn.wpcf7-form-control:hover, .wpcf7 input.wpcf7-form-control:hover[type=submit] {
    color: #fff;
    background-color: #fea33f; }

@keyframes pulse {
  to {
    box-shadow: 0 0 0 1.5rem rgba(53, 52, 49, 0); } }

.btn-pulse {
  box-shadow: 0 0 0 0 #353431;
  animation: pulse 2s infinite cubic-bezier(0.66, 0, 0, 1); }
  .btn-pulse::hover {
    animation: none; }

.section-newsletter input {
